import React from 'react';
import {HorizontalScroll, Switch} from "@vkontakte/vkui";

import IconDone from "../../icon/IconDone";

class SearchColorPick extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      colors: [
        {
          id: 1,
          colorName: 'lightgreen',
          color: '#CAEC6B',
          selected: false
        },
        {
          id: 2,
          colorName: 'yellow',
          color: '#FFEA60',
          selected: false
        },
        {
          id: 3,
          colorName: 'orange',
          color: '#FEA838',
          selected: false
        },
        {
          id: 4,
          colorName: 'red',
          color: '#FF5136',
          selected: false
        },
        {
          id: 5,
          colorName: 'pink',
          color: '#FE389B',
          selected: false
        },
        {
          id: 6,
          colorName: 'purple',
          color: '#CA33FF',
          selected: false
        },
        {
          id: 7,
          colorName: 'blue',
          color: '#433EDB',
          selected: false
        }
      ]
    }
  }

  handleToggleSelected = (e) => {
    let target = e.target;
    let temp = this.state.colors;

    while (target != 'DIV') {
      if (target.tagName == 'DIV') {
        temp = temp.map(item => (item.colorName === target.dataset.name) ? {
            id: item.id,
            colorName: item.colorName,
            color: item.color,
            selected: !item.selected
          } : item
        );
        this.setState({colors: temp})
        return;
      }
      target = target.parentNode;
    }
debugger

  };

  render() {

    const {colors} = this.state;
    const markUp = colors.map(item => {
      return <div key={item.id}
                  style={{backgroundColor: `${item.color}`}}
                  className='search-page-color-pick-button_item'
                  data-name={item.colorName}>
        {item.selected ? <IconDone currentColor='#fff'/> : null}
      </div>
    })

    return (
      <React.Fragment>
        <div className='search-page-color-handle'>
          <div className='color-handle-text'>
            <div>По цвету</div>
            <div className='color-handle-text_color'>2</div>
          </div>
          <Switch/>
        </div>
        <HorizontalScroll>
          <div onClick={this.handleToggleSelected}
               className='search-page-color-pick-button_row'>
            {markUp}
          </div>
        </HorizontalScroll>
      </React.Fragment>
    )
  }
}

export default SearchColorPick;
